@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header{
    text-align: center;
}
.logo{
    height: 50px;
}
.header nav {
    background-color: black;
    line-height: 40px;
}
.header nav a{
    text-decoration: none;
    color: white;
    margin-right: 20px;
    padding: 5px;
    font-size: 20px;
}
.header nav a:hover{
    background-color: rgba(179, 166, 166, 0.938);
    border-radius: 15px;
}

.logout{
    background-color: gray;
}
.banner-container{
    background-color: #9B46F5;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 50px;
}
.banner-container img{
    max-width: 80%;
    margin-top: 40px;
    margin-left: 80px;
}
.banner-descrition{
    margin-top: 20%;
    color:whitesmoke;
}
.banner-descrition h1{
    font-size: 3.5em;
    font-family: 'Noto Serif', serif;
}
.connection-container h1{
    color: rgba(40, 63, 138, 0.534);
}

.process{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-content: center;
    align-items: center;
    margin: 10px;
}
.single-process img{
    max-width: 90%;
}
.single-process{
    margin: 10px;
    text-align: center;
}

.founders{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.founder img{
    width: 300px;
    height: 300px;
    border-radius: 20px;
}

/* -----------------
     Media Queries
-------------------- */
/* Responsiveness for Mobile Devices */
@media only screen and (min-width:689px) and (max-width: 1000px) {
    .banner-descrition h1{
        font-size: 2em;
    }
    .founders{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .process{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        justify-content: center;
        align-items: center;
    }
}
@media only screen and (max-width:688px) {
    .banner-descrition h1{
        font-size: 1em;
        font-family: 'Noto Serif', serif;
    }
    .founders{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .process{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        justify-content: center;
        align-items: center;
    }
}
.service{
    border: 1px solid rgb(110, 107, 107);
    margin: 10px;
    padding: 15px;
    height: 540px;
    width:400px;
    border-radius: 10px;
    box-shadow: 5px 7px 35px gray;
}
.service:hover{
    border: 2px solid steelblue;
    box-shadow: 5px 10px 40px lightblue;
}
.service img, .single-comment img{
    text-align: center;
    border-radius: 10%;
    max-width: 100%;
    height: 300px;
    margin-bottom: 10px;
}

.services{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
/* -----------------
     Media Queries
-------------------- */
/* Responsiveness for Mobile Devices */
@media only screen and (min-width:901px) and (max-width: 1300px) {
    .services{
        grid-template-columns: repeat(2, 1fr);
    }
}
@media only screen and (min-width:689px) and (max-width: 900px) {
    .services{
        grid-template-columns: repeat(1, 1fr);
    }
}
@media only screen and (max-width:688px) {
    .services{
        grid-template-columns: repeat(1, 1fr);
    }
}
.footer h1{
    font-family: 'Ephesis', cursive;
}
.fab, .fas{
    width: 60px;
    height:60px;
    border-radius: 50%;
}

.notFound{
    height: 400px;
}
